
h2 {
  margin: 0;
}

.statistics {
  width: 300px;
  margin: 20px auto ;
}

.title {
  background-color: white;
  color: grey;
  padding: 20px;
    font-weight: 500;
    font-size: 15px;
    line-height: 1.1666;
    text-transform: uppercase;
}

.stat-list {
  display: grid;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  padding: 0;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;

      font-weight: 500;
      font-size: 14px;
      line-height: 1.1666;
}

.item {
  background-color: black;
  color: white;
}

.stat-list .label {
  color: white;
  margin: 0;
  padding: 8px;

        font-weight: 500;
        font-size: 10px;
        line-height: 1.1666;
}

.percentage {
  padding-bottom: 10px;
  
}

